@use "./../../styles/vars";

.about_container {
      background-size:auto;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("./../../assets/images/background-blob2.png");
      width: 100vw;
      
      
      
      &_main {
      padding: 3em 9em 12em 6em;
      
    }

    &_title {
      background-color: hsl(234, 85%, 74%, 70%);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 7rem;
      padding: 3rem 2rem 3rem 2rem;
      color:white;
      box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    }

}

.about_main {
  margin-left: 2em;
  margin-right: 2em;
    padding: 4em 1em 10em 1em;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: white;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

}

.about_main .swiper {
    width: 15em;
    height: 15em;
    margin-left: 2em;
    margin-right: 2em;
    text-align: center;
    
}


.about_main .swiper-slide {
    background-position: center;
    background-size: cover;
    border: rgba(149, 157, 165, 0.03)1px solid;
}

.about_main .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
  }

  .cube_name {
    margin-top: 20%;
    font-size: 1.2em;
    font-weight: bold;

    &_job {
      font-size: 1em;
    font-weight: bold;
    }
  }

  .overlay {
    position: absolute;
    bottom: 0;
    left: 100%;
    right: 0;
    background-color: rgba(255, 255, 255, 0.06);
    overflow: hidden;
    width: 0;
    height: 100%;
    transition: .5s ease;
  }
  

.about_main .swiper:hover .overlay {
    width: 100%;
    left: 0;
  }

  .swipe_icon {
    margin-right: 0.4em;
  }

  .text {
    position: absolute;
    top: 3%;
    left: 86%;
    font-size: 0.9em;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    white-space: nowrap;
  }

  .cube_letter {
    position: absolute;
    top: -50%;
    font-size: 15em;
  }

  .info_contain {
    margin-top: 1em;
    align-items: baseline;
    font-size: 0.9em;
    display: flex;
    flex-direction: column;
  padding: 0 1rem 2rem 1rem;
  }

  .big_mail {
    font-size: 0.8rem;

    &_potage {
      font-size: 0.8em;
    }
  }

 


  @media screen and (width < 1800px) {
    .about_main .swiper {
      width: 11em;
      height: 11em;
      
  }
  .info_contain {
    font-size: 0.7em;
  }
  .cube_letter {
    font-size: 10em;
  }
  .cube_name {
    font-size: 1em;

    &_job {
      font-size: 0.7em;
    }
  }
  }

  @media screen and (width < 1600px) {
    .about_main .swiper {
      width: 9em;
      height: 9em;
      
  }
  .info_contain {
    font-size: 0.6em;
  }
  .cube_letter {
    font-size: 9em;
  }
  .cube_name {
    font-size: 0.9em;
    margin-top: 4em;

    &_job {
      font-size: 0.6em;
    }
  }
  }


@media screen and (width < 1400px) {
    .about_main .swiper {
    width: 15em;
    height: 15em;
    margin-bottom: 10em;
    
}
.about_main {
  flex-direction: column;
  align-items: center;
}
.info_contain {
  font-size: 1em;
}






}

@media screen and (width < 700px) {
  .about_container_main {
    padding-left: 6em;
    padding-right: 6em;
    margin-bottom: 9em;
    
  }






}

@media screen and (width < 500px) {
    .about_main .swiper {
    width: 10em;
    height: 10em;
    
}
  .about_container_main {
    padding-left: 3em;
    padding-right: 3em;
    margin-bottom: 7em;
    
  }

  .about_container_title {
    padding: 1rem 1rem 1rem 1rem;

  }

  .about_main {
    padding: 1em 1em 1em 1em;

}


}

  // DARK MODE 

  #dark .about_main {
    background: vars.$linearGradientDarkModeCards; 
    color: white;
}

#dark .about_container_title {
    background: linear-gradient(to top left ,  #219898b0, #24085c  );
}

#dark .info_contain {
  color: white;
}

#dark .info_contain a{
  color: white;
}

#dark .info_contain .color-secondary {
  color:#30cfd0
}

