@use "./../../styles/vars";

.profil {
	width: 94%;
	height: 100vh;
	margin: 2rem 2rem 0 2rem;
	display: flex;
	justify-content: space-between;
	align-items: stretch;

	& a {
		color: black;
	}

	&_desc {
		width: 22%;
		height: 55%;
	}

	&_projets {
		margin-bottom: 5%;
		width: 22%;
	}

	&-middle {
		width: 54%;
		height: 86vh;
	}
}

.name_container {
	&_user {
		font-size: 1.2rem;
		margin-top: 0.5rem;
		text-align: center;
	}

	&_avatar {
		width: 10rem;
		height: 10rem;
		object-fit: cover;
		margin: 2rem auto 1rem auto;
		border-radius: 50%;
		border: solid 6px vars.$secondaryColor;
	}
}

.desc_container2 {
	height: auto;
}

.desc_container {
	background-color: #fff;
	height: 40vh;
	// min-height: 37vh;
	// max-height: 37vh;
	padding: 0 1rem 2rem 1rem;

	&_main {
		padding-left: 2rem;
		padding-bottom: 2rem;
		font-size: 1.4rem;
		font-weight: normal;
	}

	& p {
		margin: 0 1rem;
		font-size: 1rem;
		color: vars.$darkGrey;
		line-height: 1.3rem;
		// min-height:180px;
	}

	&_title {
		font-size: 0.9rem;
		color: rgb(78, 78, 78);

		& i {
			padding-right: 0.5rem;
		}
	}

	&_titlefirst {
		font-size: 1.4rem;
		font-weight: bold;
		margin-top: 5%;
	}

	&_email {
		font-size: 1rem;
	}

	&_password {
		color: rgb(0, 30, 255);
		text-decoration: underline;
	}

	&_password:hover {
		background: vars.$linearGradient;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	&_github {
		font-size: large;
		color: rgb(0, 30, 255);
		text-decoration: underline;
	}

	&_github:hover {
		background: vars.$linearGradient;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	&_linkedin {
		font-size: large;
		color: rgb(0, 30, 255);
		text-decoration: underline;
	}

	&_linkedin:hover {
		background: vars.$linearGradient;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	&_role {
		background: vars.$linearGradient;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		text-align: center;

		&-edition {
			color: grey;
			margin-top: -1rem;
		}
	}

	&_technos {
		background-color: #fff;
		padding: 0 1rem;
		margin-top: 1rem;
	}

	&_description {
		// background-color: #fff;
		// box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
		height: auto;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		&-username {
			padding: 2rem 2rem 0 2rem;
			height: 40vh;
			position: relative;
			text-align: center;
			background-color: #fff;
			margin-bottom: 1rem;
		}

		&-links-informations {
			background-color: #fff;
			padding: 2rem;
			min-height: 40vh;
			position: relative;
			word-break: break-all;
		}
	}

	&_project {
		background-color: #fff;
		padding: 3rem 0rem 0.5rem 0rem;
		margin-bottom: 5%;
		height: 87vh;
		overflow-y: scroll;
		overflow-x: hidden;
	}
}

.project-list-container {
	display: flex;
	justify-content: space-between;
	margin-top: 0.5rem;
}

.project-list-paragraph {
	margin: 0rem;
	font-size: 0.7rem;

	&-desc {
		margin: 0rem;
		font-size: 0.8rem;
		color: grey;
		margin-bottom: 0.2rem;
	}

	&-grey {
		color: grey;
		margin: 0rem;
		font-size: 0.7rem;
	}
}

.technos_logo {
	font-size: 5em;
	margin: 1rem;
}

.icone_container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;

	&_text {
		text-align: center;
		font-size: 1rem;
		background: vars.$linearGradient;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}

.project_container {
	text-align: center;

	&_main {
		background-color: aliceblue;
		border-radius: 5%;
	}
}

.card_project {
	width: 100%;
	background-color: #fff;
	box-shadow: rgb(65 70 245 / 8%) 0px 15px 10px -5px,
		rgb(113 73 245 / 4%) 0px 10px 10px -5px;
	position: relative;

	&_role {
		position: absolute;
		top: 0.5rem;
		right: 0.5rem;
		padding: 0.3rem 0.6rem;
		color: white;
		border-radius: 10px;
		font-size: 0.9rem;
		font-weight: bolder;

		&-admin {
			background-color: rgb(126, 98, 241);
		}

		&-candidat {
			background-color: rgb(29, 0, 144);
		}

		&-participant {
			background-color: rgb(0, 144, 127);
		}
	}
}

.user-technologies {
	background-color: #fff;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	min-height: 200px;
}

.card_main_title-project {
	margin-bottom: 0.5rem;
	margin-top: -0.5rem;
}

.card_img {
	height: 10rem;
	width: 100%;
	object-fit: cover;
}

.profile-edition-btns-container {
	display: flex;
	justify-content: space-between;
	align-items: baseline;
	padding-top: 3rem;

	& i {
		cursor: pointer;
		color: grey;
	}

	& i:hover {
		cursor: pointer;
		color: rgb(78, 78, 78);
	}
}

.margin0 {
	margin: 0;
}

.padding-on-edition {
	padding-top: 2rem;
	overflow-y: scroll;
}

.min-height-technos-container {
	height: 46vh;
	overflow-y: scroll;
	padding-bottom: 1rem;
}

.user-available {
	margin-bottom: 2rem;
	padding-top: 2rem;
}

.user-password {
	margin-top: 2rem;
}

.technologies-icon-container {
	background-color: vars.$backgroundLight;
	display: flex;
	align-items: center;
	border-radius: 5px 8px 8px 5px;
	text-transform: capitalize;
	font-size: 0.9rem;
	margin-bottom: 0.8rem;
	width: 100%;

	& i {
		margin-right: 0.6rem;
		color: white;
		padding: 0.6rem 0.7rem;
		font-size: 1.2rem;
		border-radius: 5px;
	}
}

.user_desc_link {
	background: vars.$linearGradient;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: bolder;
	cursor: pointer;
	margin-left: 1rem;
}

.user-info-input-edition {
	display: flex;
	align-items: center;
	margin-bottom: 0.5rem;
}

@media screen and (width < 1600px) {
	.profil {
		width: 93vw;
	}
}

@media screen and (width < 600px) {
	.profil {
		width: 88%;
		height: auto;
		margin: 0;
		flex-wrap: wrap;

		&_desc {
			width: 100%;
			height: auto !important;
		}

		&_projets {
			width: 100%;
		}

		&-middle {
			width: 100%;
			margin-top: 1rem;
			height: auto;
		}

		.desc_container {
			height: auto !important;
			padding: 0;

			&_main {
				padding: 1rem;
			}

			&_role {
				margin-bottom: 1rem;
			}
		}

		.desc_container_description {
			height: auto;

			&-username {
				height: auto;
			}

			&-links-informations {
				padding: 2rem 1rem;
			}
		}

		.min-height-technos-container {
			min-height: auto;
			max-height: auto;
			height: 100%;
			overflow-y: auto;
		}

		.user-description-texte {
			margin-bottom: 2rem;
			text-align: justify;
			padding-bottom: 2rem;
			height: auto !important;
		}
	}

	.technologies-icon-container {
		width: 80%;
	}
}

// DARK MODE

#dark .desc_container {
	background: vars.$linearGradientDarkModeCards;
	color: white;

	& p {
		color: rgba(255, 255, 255, 0.9);
	}
}

#dark .desc_container_description-username {
	background: vars.$linearGradientDarkModeCards;
	color: white;
}

#dark .desc_container_description-links-informations {
	background: vars.$linearGradientDarkModeCards;
	color: white;
}

#dark .desc_container_technos {
	background: vars.$linearGradientDarkModeCards;
	color: white;
}

#dark .user-technologies {
	background: transparent;
	color: white;

	& h4 {
		color: white;
	}

	& span {
		background: vars.$linearGradientDarkMode;
	}
}

#dark .desc_container_project {
	background: vars.$linearGradientDarkModeCards;
	color: white;
}

#dark .card_project {
	background: vars.$linearGradientDarkModeCards;
	color: white;
}

#dark .desc_container_title {
	color: white;

	& a {
		color: white;
	}
}

#dark .desc_container_role {
	background: #30cfd0;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

#dark .dashboard-edit-input {
	background-color: #323c5a;
	color: white;
}

#dark .form-technologies-empty {
	background-color: transparent;
}

#dark .desc_container_user-links-dark {
	& i {
		background: #30cfd0;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}

#dark .user_desc_link {
	background: #30cfd0;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	margin-left: 1rem;
}

#dark .name_container_avatar {
	border: solid 6px #3c24b1;
}

#dark .card_main_title-project {
	color: white;
}
